<template><section><h1>Select</h1>
<blockquote>
<p>选择器</p>
</blockquote>
<h2>代码示例</h2>
<p><test></test></p>

<h2>API</h2>
<h3>Select</h3>
<table>
<thead>
<tr>
<th>参数</th>
<th style="text-align:left">说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td style="text-align:left">选择器当前选中的数据，请使用<code>v-model</code>获取及设置</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>size</td>
<td style="text-align:left">选择器尺寸，可选值 <code>sm</code>/<code>md</code>/<code>lg</code></td>
<td><code>String</code></td>
<td><code>md</code></td>
</tr>
<tr>
<td>color</td>
<td style="text-align:left">选择器聚焦时的颜色, 支持两种格式<code>#f00</code>/<code>rgb(255,255,255)</code></td>
<td><code>String</code></td>
<td>-</td>
</tr>
<tr>
<td>label</td>
<td style="text-align:left">标题</td>
<td><code>String</code></td>
<td><code>请选择</code>/<code>已选择</code></td>
</tr>
<tr>
<td>data</td>
<td style="text-align:left">选择器数据</td>
<td><code>Array</code></td>
<td>-</td>
</tr>
<tr>
<td>show_by</td>
<td style="text-align:left">指定选项展示的值, 默认展示data[index]</td>
<td><code>String</code></td>
<td>-</td>
</tr>
<tr>
<td>value_by</td>
<td style="text-align:left">指定选项返回的值, 默认返回data[index]</td>
<td><code>String</code></td>
<td>-</td>
</tr>
<tr>
<td>float</td>
<td style="text-align:left">标题是否浮动</td>
<td><code>Boolean</code></td>
<td><code>false</code></td>
</tr>
<tr>
<td>disabled</td>
<td style="text-align:left">是否禁用</td>
<td><code>Boolean</code></td>
<td><code>false</code></td>
</tr>
<tr>
<td>default_first</td>
<td style="text-align:left">是否默认选中第一个选项</td>
<td><code>Boolean</code></td>
<td><code>false</code></td>
</tr>
<tr>
<td>clear_invalid</td>
<td style="text-align:left">是否自动清空无效值, 如果value并未存在data数据中</td>
<td><code>Boolean</code></td>
<td><code>false</code></td>
</tr>
</tbody>
</table>
<table>
<thead>
<tr>
<th>事件</th>
<th style="text-align:left">说明</th>
<th>类型</th>
<th>使用</th>
<th>结果</th>
</tr>
</thead>
<tbody>
<tr>
<td>close</td>
<td style="text-align:left">关掉当前Select选择器</td>
<td>触发事件</td>
<td><code>component.$emit('close')</code></td>
<td>-</td>
</tr>
<tr>
<td>form-verify</td>
<td style="text-align:left">验证当前选择器是否选中</td>
<td>触发事件</td>
<td><code>component.$emit('form-verify')</code>/<code>this.$root.$emit('form-verify')</code></td>
<td>-</td>
</tr>
</tbody>
</table>
<h3>SelectItem</h3>
<table>
<thead>
<tr>
<th>参数</th>
<th style="text-align:left">说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>active</td>
<td style="text-align:left">选中状态</td>
<td><code>Boolean</code></td>
<td><code>false</code></td>
</tr>
</tbody>
</table>
</section></template>

<script>
  import test from '@/pages/demo/Select.vue';

  export default {
    components: {
      test
    }
  }
</script>


