<template><section><h1>Input</h1>
<blockquote>
<p>输入栏</p>
</blockquote>
<h2>代码示例</h2>
<p><test></test></p>

<h2>API</h2>
<table>
<thead>
<tr>
<th>参数</th>
<th style="text-align:left">说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td style="text-align:left">输入框的值，请使用<code>v-model</code>获取及设置</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>size</td>
<td style="text-align:left">输入框尺寸，可选值 <code>sm</code>/<code>md</code>/<code>lg</code></td>
<td><code>String</code></td>
<td><code>md</code></td>
</tr>
<tr>
<td>color</td>
<td style="text-align:left">输入框聚焦时的颜色, 支持两种格式<code>#f00</code>/<code>rgb(255,255,255)</code></td>
<td><code>String</code></td>
<td>-</td>
</tr>
<tr>
<td>label</td>
<td style="text-align:left">标题</td>
<td><code>String</code></td>
<td>-</td>
</tr>
<tr>
<td>placeholder</td>
<td style="text-align:left">占位文本/提示文本</td>
<td><code>String</code></td>
<td>-</td>
</tr>
<tr>
<td>type</td>
<td style="text-align:left">输入类型</td>
<td><code>String</code></td>
<td><code>text</code></td>
</tr>
<tr>
<td>verify</td>
<td style="text-align:left">验证内容，可选值 <code>phone</code>/<code>password</code>/<code>email</code>/<code>empty</code></td>
<td><code>String</code></td>
<td>-</td>
</tr>
<tr>
<td>reg</td>
<td style="text-align:left">自定义验证正则</td>
<td><code>String</code>/<code>RegExp</code></td>
<td>-</td>
</tr>
<tr>
<td>tip</td>
<td style="text-align:left">验证错误提示</td>
<td><code>String</code></td>
<td>-</td>
</tr>
<tr>
<td>disabled</td>
<td style="text-align:left">是否禁用</td>
<td><code>Boolean</code></td>
<td><code>false</code></td>
</tr>
<tr>
<td>format</td>
<td style="text-align:left">是否格式化内容，将全角符号转化为半角, <code>，</code> =&gt; <code>,</code></td>
<td><code>Boolean</code></td>
<td><code>false</code></td>
</tr>
<tr>
<td>readonly</td>
<td style="text-align:left">只读</td>
<td><code>Boolean</code></td>
<td><code>false</code></td>
</tr>
<tr>
<td>with_border</td>
<td style="text-align:left">是否带边框</td>
<td><code>Boolean</code></td>
<td><code>true</code></td>
</tr>
<tr>
<td>rows</td>
<td style="text-align:left"><code>textarea</code>类型有效，指定默认行数</td>
<td><code>Number</code></td>
<td><code>3</code></td>
</tr>
<tr>
<td>autosize</td>
<td style="text-align:left"><code>textarea</code>类型有效，内容撑开高度</td>
<td><code>Boolean</code></td>
<td><code>false</code></td>
</tr>
</tbody>
</table>
<table>
<thead>
<tr>
<th>事件</th>
<th style="text-align:left">说明</th>
<th>类型</th>
<th>使用</th>
<th>结果</th>
</tr>
</thead>
<tbody>
<tr>
<td>form-verify</td>
<td style="text-align:left">验证当前选择器是否选中</td>
<td>触发事件</td>
<td><code>component.$emit('form-verify')</code>/<code>this.$root.$emit('form-verify')</code></td>
<td>-</td>
</tr>
<tr>
<td>error</td>
<td style="text-align:left">手动触发错误提示（需要手动清除提示，否则一直存在）</td>
<td>触发事件</td>
<td><code>component.$emit('error', '错误提示')</code></td>
<td>-</td>
</tr>
<tr>
<td>correct</td>
<td style="text-align:left">手动触发正确提示</td>
<td>触发事件</td>
<td><code>component.$emit('correct')</code></td>
<td>-</td>
</tr>
<tr>
<td>clear</td>
<td style="text-align:left">手动清除提示</td>
<td>触发事件</td>
<td><code>component.$emit('clear')</code></td>
<td>-</td>
</tr>
<tr>
<td>verify</td>
<td style="text-align:left">返回验证结果</td>
<td>监听事件</td>
<td><code>@verify=&quot;result = argument[0]&quot;</code></td>
<td><code>true</code>/<code>false</code></td>
</tr>
<tr>
<td>enter</td>
<td style="text-align:left">用户按下Enter键时触发</td>
<td>监听事件</td>
<td><code>@enter=&quot;dosomething&quot;</code></td>
<td>-</td>
</tr>
</tbody>
</table>
</section></template>

<script>
  import test from '@/pages/demo/Input.vue';

  export default {
    components: {
      test
    }
  }
</script>


