<template><section><h1>Swipe</h1>
<blockquote>
<p>走马灯</p>
</blockquote>
<h2>代码示例</h2>
<p><test></test></p>

<h2>API</h2>
<table>
<thead>
<tr>
<th>参数</th>
<th style="text-align:left">说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td style="text-align:left">Swipe的当前下标，请使用<code>v-model</code>获取及设置</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>preview</td>
<td style="text-align:left">预览展示的滑块数目</td>
<td><code>Number</code></td>
<td>1</td>
</tr>
<tr>
<td>between</td>
<td style="text-align:left">每个滑块之间的间距</td>
<td><code>Number</code></td>
<td>0</td>
</tr>
<tr>
<td>trigger</td>
<td style="text-align:left">index的触发时机, 可选值<code>change</code>/<code>progress</code></td>
<td><code>String</code></td>
<td>change</td>
</tr>
<tr>
<td>arrow</td>
<td style="text-align:left">箭头, 可选值<code>true</code>/<code>false</code>/<code>'hover'</code></td>
<td><code>String, Boolean</code></td>
<td>false</td>
</tr>
<tr>
<td>outer_arrow</td>
<td style="text-align:left">是否显示外部箭头, 可选值<code>true</code>/<code>false</code></td>
<td><code>Boolean</code></td>
<td>false</td>
</tr>
<tr>
<td>index</td>
<td style="text-align:left">显示下标, 可选值<code>true</code>/<code>false</code></td>
<td><code>Boolean</code></td>
<td>false</td>
</tr>
<tr>
<td>index_trigger</td>
<td style="text-align:left">下标触发滑动的方式, 可选值<code>click</code>/<code>hover</code>/<code>false</code></td>
<td><code>String</code>/<code>Boolean</code></td>
<td>click</td>
</tr>
<tr>
<td>sliding_num</td>
<td style="text-align:left">同时滑动的滑块个数</td>
<td><code>Number</code></td>
<td>1</td>
</tr>
<tr>
<td>loop</td>
<td style="text-align:left">无限循环滑动</td>
<td><code>Boolean</code></td>
<td>true</td>
</tr>
<tr>
<td>free</td>
<td style="text-align:left">滑块自由滑动</td>
<td><code>Boolean</code></td>
<td>false</td>
</tr>
<tr>
<td>coverflow</td>
<td style="text-align:left">coverflow过渡效果</td>
<td><code>Boolean</code></td>
<td>false</td>
</tr>
<tr>
<td>autoplay</td>
<td style="text-align:left">自动滑动</td>
<td><code>Boolean</code></td>
<td>false</td>
</tr>
<tr>
<td>update_by</td>
<td style="text-align:left">指定更新数据源，Swipe会监听它，用于更新Swipe状态</td>
<td>-</td>
<td>-</td>
</tr>
</tbody>
</table>
<table>
<thead>
<tr>
<th>事件</th>
<th style="text-align:left">说明</th>
<th>类型</th>
<th>使用</th>
<th>结果</th>
</tr>
</thead>
<tbody>
<tr>
<td>slide-to</td>
<td style="text-align:left">滑动到指定index</td>
<td>触发事件</td>
<td><code>component.$emit('slide-to', 0)</code></td>
<td>-</td>
</tr>
<tr>
<td>slide-next</td>
<td style="text-align:left">滑动到后一个</td>
<td>触发事件</td>
<td><code>component.$emit('slide-next')</code></td>
<td>-</td>
</tr>
<tr>
<td>slide-prev</td>
<td style="text-align:left">滑动到前一个</td>
<td>触发事件</td>
<td><code>component.$emit('slide-prev')</code></td>
<td>-</td>
</tr>
</tbody>
</table>
</section></template>

<script>
  import test from '@/pages/demo/Swipe.vue';

  export default {
    components: {
      test
    }
  }
</script>


